.site-header {
  display: inline-block;
  background-color: white;
  color: black;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center; }
  .site-header div.identity {
    color: gray;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex; }
    .site-header div.identity div.site-logo img {
      height: 2.5rem;
      width: 2.5rem;
      margin: 0.5rem;
      margin-bottom: 0; }
    .site-header div.identity .site-branding {
      text-align: center;
      display: inline-block;
      -webkit-box-pack: start;
      -ms-flex-pack: start;
      justify-content: start; }
      .site-header div.identity .site-branding p.site-description {
        font-size: 0.8rem;
        padding: 0; }
      .site-header div.identity .site-branding p.site-title {
        padding: 0;
        font-size: 1.2rem;
        text-transform: uppercase;
        font-weight: bold; }
        .site-header div.identity .site-branding p.site-title a {
          color: black; }
  .site-header .navigation ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    padding: 0;
    -ms-flex-line-pack: center;
    align-content: center; }
  .site-header .navigation li {
    text-align: center;
    -webkit-box-flex: 1;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
    list-style: none outside;
    margin-bottom: 1.5rem; }
  .site-header .navigation li a {
    padding: 0.5rem 1rem;
    color: black; }
  .site-header .navigation li a:focus,
  .site-header .navigation li a:hover {
    color: white;
    background: orangered;
    border-radius: 4rem; }

.site-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding: var(--spacing-site, 2em);
  -webkit-box-flex: 1;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto;
  margin: 0 auto; }
  .site-content a {
    color: black; }

.end {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
  justify-content: space-around; }

.site-footer {
  padding-top: 1rem;
  background-color: black;
  color: white; }
  .site-footer ul > li > a {
    color: white; }

ul.posts {
  list-style-type: none;
  padding-left: 0; }

ul.posts li {
  display: block;
  -webkit-animation: slideIn 0.6s ease 1 both;
  animation: slideIn 0.6s ease 1 both;
  list-style: none outside;
  padding: 0.5rem 0; }

ul.posts li a {
  display: block;
  text-decoration: none;
  color: black; }

@-webkit-keyframes slideIn {
  from {
    opacity: 0;
    -webkit-transform: translateX(-5rem);
    transform: translateX(-5rem); }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0); } }

@keyframes slideIn {
  from {
    opacity: 0;
    -webkit-transform: translateX(-5rem);
    transform: translateX(-5rem); }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0); } }

ul.posts li:nth-child(2) {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

ul.posts li:nth-child(3) {
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s; }

ul.posts li:nth-child(4) {
  -webkit-animation-delay: 0.9s;
  animation-delay: 0.9s; }

ul.posts li:nth-child(5) {
  -webkit-animation-delay: 1.2s;
  animation-delay: 1.2s; }

ul.posts li:nth-child(6) {
  -webkit-animation-delay: 1.5s;
  animation-delay: 1.5s; }

