  margin-right: 2rem; }

/*Whitespace reset*/
* {
  padding: 0;
  margin: 0; }

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

/*typography*/
html {
  font: var(--font-size-initial, 14px)/1.5 "Verdana", sans-serif; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-lv2, 16px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv3, 18px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-lv4, 21px); } }

body, table, td, th, textarea, option, select, form, input {
  font: inherit; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2; }

h1 {
  font-size: 3.998em; }

h2 {
  font-size: 2.827em; }

h3 {
  font-size: 1.999em; }

h4 {
  font-size: 1.414em; }

p, ul, ol {
  padding-bottom: 1.5em; }

ul, ol {
  padding-left: 1.5rem; }

li {
  list-style-position: inside; }

:root {
  --font-size-initial: 14px;
  --font-size-lv2: 16px;
  --font-size-lv3: 18px;
  --font-size-lv4: 21px; }
